<template>
  <div id="main">

    <div style="margin: 35px 15px;">
      <ul style="width:100%">

        <li style="width:100%">
          <div class="panel">
            <div class="title">
              <div style="height:30px"></div>
            </div>
            <div v-if="noneData===true">
              <div>
                <ul class="margin10">
                  <li v-for="item in dataList" :key="item.id" style="display: unset!important;width:100%">
                    <div class="prizeName">
                      {{item.prizeName}}
                    </div>
                    <div>
                      <!--<img v-if="item.prizeImg!==null" :src="path+item.prizeImg" width="65%">-->
                      <my-img :imageSrc="path+item.prizeImg" errorType="img" width="100%" height="auto"/>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <none-data class="index-none-data" v-else/>
          </div>
        </li>
        <li>
          <div class="panel">
            <div class="title">
              <div style="height:30px"></div>
            </div>
            <div>
              <ul style="text-align:left;margin:10px">
                <li>
                  ()5()
                </li>
                <li>()</li>
                <li>1.31</li>
                <li>2.</li>
                <li>3.!!</li>
                <li>4.</li>
                <li>()</li>
                <li>24   IP 
                  
                </li>
                <li>()</li>
                <li>1.</li>
                <li>2.</li>
                <li>3.</li>
                <li>4.</li>
              </ul>
            </div>
          </div>
        </li>

      </ul>
    </div>
  </div>
</template>

<script>
  import { getPrizeList } from '@/api/Service';
  import NoneData from './common/NoneData';
  import ImageError from './common/ImageError';
  import {config} from '../assets/js/config';
  import store from '@/assets/js/store';

  export default {
    components: {
      'none-data': NoneData,
      'my-img': ImageError
    },
    data () {
      return {
        dataList: [],
        path: config.img_url,
        noneData: false//,false
      }
    },
    created: function () {
      getPrizeList({uuid: store.state.uuid}, res => {
        console.log('prize',res)
        if(res.data.sysPrizes){
          this.dataList = res.data.sysPrizes;
          this.noneData = !!this.dataList;
        }
      });
    }
  }
</script>

<style scoped>
  #main {
    background: #ecf0f5;
    min-height: 100vh;
  }

  .marT10 {
    margin-top: 10px;
  }

  .panel {
    width: 100%;
    background: #fff;
  }

  .title {
    background: #3cb371;
    height: 35px;
    line-height: 35px;
    color: #fff;
  }

  .prizeName{
    font-size: 18px;
    padding: 5px;
    background: #ff3b30;
    color: #ffffff;
  }

  ul {
    list-style: none;

    color: #757575;
    font-size: 14px;
    margin-bottom: 55px;
  }

  li {
    display: inline-flex;
    margin-bottom: 8px;

  }

  .margin10 {
    margin: 10px;
  }

  .overflow {
    overflow: hidden;
  }

</style>



