<template>
    <div class="page msg_success js_show">
        <div class="weui-msg">
            <div class="weui-msg__icon-area"><i class="weui-icon-success weui-icon_msg"></i></div>
            <div class="weui-msg__text-area">
                <h2 class="weui-msg__title"></h2>
                <p class="weui-msg__desc">{{data.num}}{{data.payName}}{{ticketNum}}<router-link to="/list" tag="a"></router-link></p>
            </div>
            <div class="weui-msg__opr-area">
                <p class="weui-btn-area">
                    <a class="weui-btn weui-btn_primary" @click="goBackToPresent"></a>
                    <a class="weui-btn weui-btn_default" @click="goToIndex"></a>
                </p>
            </div>
            <div class="weui-msg__extra-area">
                <div class="weui-footer">
                    <p class="weui-footer__links">
                        <a href="javascript:void(0);" class="weui-footer__link"></a>
                    </p>
                    <p class="weui-footer__text">Copyright  2018-2019 </p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      data: {}
    };
  },
  created: function() {
    console.log(this.$route.params);
    this.data = this.$route.params.payInfo;
  },
  methods: {
    //
    goBackToPresent: function() {
      this.$router.push({
        name: "Present",
        params: { userInfo: this.$route.params.payInfo.userInfo }
      });
    },
    //
    goToIndex: function() {
      this.$router.push({name:"Index",params:{uuid: sessionStorage.getItem('uuid')}});
    }
  },
  computed: {
    ticketNum: function() {
      if(this.data.price===2){
        
      }
      return this.data.price * 5 * this.data.num;
    }
  }
};
</script>

<style>
</style>



